	module.exports = {"data":{"markdownRemark":{"html":"<p>早晨起来，外面在下雨，风也不小。到市政府法制办去开会，是征求各方面的专家对\"二十一世纪上海国际化大都市政府法制建设\"课题的意见。</p>\n<p>社会现代化的过程，就是法制化的过程，无法不能说是现代化。如有人说到，在美国的一些城市，法律规定吃了大蒜之类的东西，要半个小时以后才能到公共场所去;规定在商店广告减价销售的时期内，必须有店内所有的商品，不能没有;规定政府职员的责任很明确。法律在国外是一个天大的规则，任何人不能违背。在中国，法律还没有达到这样的地位，法律是一个天大的概念，很少有人能说清楚。这就是差别。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0056.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0056.md","frontmatter":{"title":"3月12日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0056.md absPath of file >>> MarkdownRemark"}}

